import React, { useState } from 'react';

const CardTodaysPromo = ({ img, alt, productName, qty, discount, discountPrice, actualPrice }) => {
  const [isQtyLow, setIsQtyLow] = useState(() => qty < 6);

  return (
    <div className='card-todays-promo'>
      <div className="img-wrapper">
        <div className="discount">{discount} OFF</div>
        <img src={img} alt={alt} />
      </div>
      <div className="card-desc">
        {/* <div className="card-desc-left"> */}
        <div className="product-name">{productName}</div>
        <div className="qty-left"><span style={isQtyLow ? { color: '#FF0000' } : { color: '#026BB9' }}>{qty}</span> Produk Tersisa</div>
        {/* </div> */}
        {/* <div className="card-desc-right"> */}
        <div className="discount-price">Rp. {discountPrice}</div>
        <div className="actual-price">Rp. {actualPrice}</div>
        {/* </div> */}
      </div>
    </div>
  )
}

export default CardTodaysPromo
