import React, { useContext } from 'react';
import { AppContext } from './../context/AppContext';
import CardTodaysPromo from './CardTodaysPromo';
import fire from '../../assets/highlight-icon/fire.svg';
import MenuSlider from './MenuSlider';

const TodaysPromo = () => {
  const { todaysPromoProducts } = useContext(AppContext);

  return (
    <div className='todays-promo'>
      <div className="todays-promo-head">
        <h2>Promo Hari Ini<span><img src={fire} alt="fire icon" /> <p>Hot!</p></span></h2>
        <div className="see-all-at-promo">Lihat Semua</div>
      </div>
      <MenuSlider />
      <div className="todays-promo-cards">
        {todaysPromoProducts.map(product => (
          <CardTodaysPromo img={product.img} alt={product.alt} productName={product.productName} qty={product.qty} discount={product.discount} discountPrice={product.discountPrice} actualPrice={product.actualPrice} />
        ))}
      </div>
    </div>
  )
}

export default TodaysPromo
