import React from 'react';
import tasPink from '../../assets/img/tas-pink/tas-pink.png';
import dompetCoklat from '../../assets/img/dompet-coklat/dompet-coklat.png';
import sofaIjo from '../../assets/img/sofa-ijo/sofa-ijo.png';
import CardProductPerCategory from './CardProductPerCategory';
import MenuSlider from './MenuSlider';

const ProductPerCategory = () => {
  return (
    <div className='product-per-category'>
      <div className='product-per-category-head'>
        <h2>Belanja per Kategori</h2>
        <div className="see-all-at-category">Lihat Semua</div>
      </div>
      <MenuSlider />
      <div className="ppc-cards">
        <CardProductPerCategory img={tasPink} alt='tas pink' category='Aksesoris Wanita' />
        <CardProductPerCategory img={dompetCoklat} alt='dompet coklat' category='Aksesoris Pria' />
        <CardProductPerCategory img={sofaIjo} alt='sofa ijo' category='Perabotan Rumah Tangga Banget' />
      </div>
    </div>
  )
}

export default ProductPerCategory
